:root {
  --text-color: #303030;
  --body-bg-color: #e4e3e3;
  --header-bg-color: linear-gradient(#0ff, #9198e5);
  --header-content-color: #fff;
  --aside-bg-color: #c5c2c2;
  --card-bg-color: #fff;
  --photo-bg-color: #00c3ff;
  --border-color: #6d6d6d;
  --btn-bg-color: #76c8d6;
  --btn-bg-hover-color: #7ff;
  --btn-bg-active-color: #9198e5;
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

html {
  scroll-behavior: smooth;
  font-family: sans-serif;
}

body {
  background-color: var(--body-bg-color);
}

a {
  color: var(--text-color);
  text-decoration: none;
}

img {
  width: 100%;
}

.header {
  z-index: 5;
  width: 100%;
  height: 60px;
  background: var(--header-bg-color);
  justify-content: center;
  align-items: center;
  padding: 0 10px;
  display: flex;
  position: fixed;
  top: 0;
  left: 0;
  box-shadow: 0 0 8px #000;
}

.header__title {
  color: var(--header-content-color);
  text-shadow: 0 1px 4px #000;
  font-weight: 800;
}

@media (max-width: 575px) {
  .header {
    justify-content: flex-end;
  }

  .header__title {
    font-size: 26px;
  }
}

.burger {
  display: none;
}

@media (max-width: 991px) {
  .burger {
    width: 40px;
    height: 40px;
    display: block;
    position: absolute;
    top: 50%;
    left: 10px;
    transform: translateY(-50%);
  }

  .burger span {
    background-color: var(--header-content-color);
    width: 100%;
    height: 4px;
    display: block;
    position: absolute;
    top: 17px;
    left: 0;
  }

  .burger span:before, .burger span:after {
    background-color: var(--header-content-color);
    content: "";
    width: 100%;
    height: 100%;
    display: block;
    position: absolute;
    right: 0;
  }

  .burger span:before {
    top: -10px;
  }

  .burger span:after {
    bottom: -10px;
  }

  .body.blocked {
    overflow: hidden;
  }

  .burger.active span {
    background: none;
  }

  .burger.active span:before {
    top: 0;
    transform: rotate(45deg);
  }

  .burger.active span:after {
    bottom: 0;
    transform: rotate(-45deg);
  }
}

.main {
  display: flex;
}

.aside {
  z-index: 3;
  width: 240px;
  height: 100%;
  background-color: var(--aside-bg-color);
  padding: 90px 15px 0;
  position: fixed;
  top: 0;
  left: 0;
  box-shadow: 0 0 8px #000;
}

@media (max-width: 991px) {
  .aside {
    width: 50%;
    height: auto;
    transition: left .3s linear;
    left: -600px;
  }

  .aside.active {
    left: 0;
  }
}

@media (max-width: 575px) {
  .aside {
    width: 100%;
    height: 100%;
    padding: 70px 15px 0;
  }
}

.menu {
  border: 1px solid var(--border-color);
  -webkit-user-select: none;
  user-select: none;
  border-radius: 4px;
}

.menu__item {
  text-align: center;
  padding-top: 15px;
}

.menuItem__name {
  color: var(--text-color);
  font-size: 14px;
  font-weight: 400;
}

.menuItem__search {
  width: 160px;
  color: var(--text-color);
  padding: 5px;
  font-size: 14px;
}

.menuItem__search:focus {
  outline: none;
}

.menuItem__btnBox {
  flex-wrap: wrap;
  justify-content: center;
  display: flex;
}

.menuItem__btnWrapper {
  padding: 8px;
}

.menuItem__btn {
  width: 70px;
  height: 30px;
  text-align: center;
  color: var(--text-color);
  background-color: var(--btn-bg-color);
  border: 1px solid var(--border-color);
  border-radius: 5px;
  justify-content: center;
  align-items: center;
  font-size: 16px;
  transition: background-color .1s linear;
  display: flex;
}

.menuItem__btn:hover {
  background-color: var(--btn-bg-hover-color);
  cursor: pointer;
}

input:checked ~ .menuItem__btn {
  background-color: var(--btn-bg-active-color);
}

.menuItem__radio {
  position: absolute;
  left: -999px;
}

.menuItem__btnReset {
  border: 1px solid var(--border-color);
  outline: none;
  margin: 20px auto;
  display: block;
}

@media (max-width: 991px) {
  .menu {
    border: 0;
  }

  .menuItem__search {
    font-size: 20px;
  }

  .menuItem__btn {
    height: 48px;
  }
}

@media (max-width: 575px) {
  .menu_item {
    padding: 0 0 5px;
  }

  .menuItem__btnBox {
    padding-bottom: 0;
  }

  .menuItem__btnReset {
    margin-top: 20px;
  }
}

